@import 'colors';
@import 'buttons';

* {
  font-family: 'Roboto', sans-serif;
  text-align: center; 
  color: $blue;
  box-sizing: border-box;
}


a{
  text-decoration: none;
  padding: 1vw;
  &:hover{
    cursor: pointer;
    color:$blue;
    font-size: 20vh;
  }
}

td, th{
  padding: none;
  margin: none;
}

// navbar and app stuff 
.main-nav{
  top: 0vh;
  max-height: 5vw;
}

.link{
  top: 0vh;
  display: inline-block;
  color: $blue;
  padding: 0 5vw 0 5vw;
  max-height: 8vh; 
}

.active-link{
  color: $yellow;
}
// home page stuff

.title-holder{
  position: absolute;
  top: 45%;
  padding: 1vw 1vw;
  left: 0vh;
  align-content: center;
  background-color: white;
  box-shadow: 0.1vw 0.5vw 0.5vw #888888;
}
.title{
  font-size: 5vh;
  margin: 0vh;
  padding: 0vh;   
  color: $blue;
  z-index: 1;
}

.subtitle{
  font-size: 5vh;
}

.section-block{
  position: absolute;
  width: 50vw;
  height: 50vh;
  cursor: pointer;
  z-index: -1;
  &:hover{
    background: darken($blue, 3);
    color: black;
  }
}

.section-label{
  font-size: 5vh;
  color: white;
}

#projects-block{
  top: 0vh;
  background-color: pink;
  &:hover{
    background: darken(pink, 5%);
  }
}
#projects-section-label{
  color: $blue;
  top: 10vh;
  left: 10vh;
}

#bio-block{
  top: 0vh;
  right: 0vh;
  background-color: $blue;
  &:hover{
    background: darken($blue, 5%);
  }
}

#bio-section-label{
  color: white;
    top: 10vh;
    right: 10vh;
}

#resume-block{
  background-color: $green;
  bottom: 0vh;
  &:hover{
    background: darken($green, 5%);
  }
}

#resume-section-label{
  color: $blue;
}

#contact-block{
  bottom: 0vh;
  right: 0vh;
  background-color: $yellow;
  &:hover{
    background: darken($yellow, 5%);
  }
}

#contact-section-label{
  color: white;
}


//featured project

.featured-project{
  width: 80vw;
  box-shadow: 1vw 0.1vw 1vw #888888;
  margin: 0vw 10vw;
}

.featured-project-image{
  width: 60vw;
  height: 30vw;
}


