.project-text{
  text-align: left;
  margin: 1.5 vh;
}


ul {
  display: flex;
  min-height: 100vh;
  max-height: 100vh;
  margin: 0;
  padding: 0;
  overflow: hidden;
  list-style-type: none;
}

li {
  flex: 1;
  display: flex;
  align-items: stretch;
  padding: 20px;
  background:$blue;
  transition: all 2.5s ease;
  &:hover {
    background: darken($blue, 8);
    cursor: pointer;
  }
  &.clickedProject {
    flex: 5;
    background: white;
    cursor: default;
    h2 {
      color:$blue;
    }
    .project-content {
      flex: 4;
      flex-direction: column;
      //width: 40vw;
      opacity: 1;
      transform: scaleX(1);
      color: $blue;
      margin: 0;
    }
    .project-title{
      color: $blue;  
      h2 {
      color: $blue;
    }
    }
  }
  
  .project-title {
    flex: 1;
    display: flex;
    align-items: center;
    width: 100%;
    margin: 0;
    padding: 0;
    text-align: center;
    color: white;
    h2 {
      margin: 0;
      transform: rotate(-90deg);
      white-space: nowrap;
      color: white;
    }
  }

  .project-content {
    flex: 1;
    display: flex;
    align-items: left;
    margin: 0;
    padding: 0;
    opacity: 0;
    transition: all 2.5s .1s ease-out;
  }
}

.project-image{
  width: 20vw;
  height: 12vw;
}

//gallery links
.wrapper{
  margin:3em 0;
}
a,a:visited,a:hover,a:active{
  -webkit-backface-visibility:hidden;
          backface-visibility:hidden;
	position:relative;
  transition:0.5s color ease;
	text-decoration:none;
	color:#81b3d2;
	font-size:3vh;
}
a:hover{
  color: $blue;
}
a.before:before,a.after:after{
  content: "";
  transition:0.5s all ease;
  -webkit-backface-visibility:hidden;
          backface-visibility:hidden;
  position:absolute;
}
a.before:before{
  top:-0.25em;
}
a.after:after{
  bottom:-0.25em;
}
a.before:before,a.after:after{
  height:5px;
  height:0.1rvh;
  width:0;
  background:$blue;
}

a.before:hover:before,a.after:hover:after{
  width:100%;
}
.square{
  box-sizing:border-box;
  margin-left:-0.4em;
  position:relative;
  font-size:3vh;
  overflow:hidden;
}
.square a{
  position:static;
  font-size:100%;
  padding:0.2em 0.4em;
}
.square:before,.square:after{
  content: "";
  box-sizing:border-box;
  transition:0.25s all ease;
  -webkit-backface-visibility:hidden;
          backface-visibility:hidden;
  position:absolute;
  width:5px;
  width:0.35rem;
  height:0;
  background:$blue;
}
.square:before{
  left:0;
  bottom:-0.2em;
}
.square:after{
  right:0;
  top:-0.2em;
}
.square a:before{
  left:0;
  transition:0.25s all ease;
}
.square a:after{
  right:0;
  transition:0.25s all ease;
}
.square:hover:before,.square:hover:after{
  height:calc(100% + 0.4em);
}
.square:hover a:before,.square:hover a:after{
  width:100%;
}

//resume

.resume{
  all: none;
}


@media only screen and (min-width: 1000px) {
  /* Minor styling: larger image and fonts to match largest screen size */
  .link{
  padding: 1% 1%;
}

.icon-menu {
  font-size: 3.6vh;
}

.title-holder{
  top: 40vh;
  padding: 1vw 15vw;
  left: 15vw;
}
.title{
  font-size: 8vh;
}

.section-label{
  font-size: 8vh;
}

}




